@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@100;300;500;600;700;800;900&family=Montserrat:wght@100;200;300;400;500;600;700;800&family=Raleway:wght@100;200;300;400;500;600&display=swap');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,body{
  scroll-behavior: smooth;
}
.header{
  width: 100%;
  height: 80vh;
  background-image: linear-gradient(rgba(0,0,0,0.9),rgba(0,0,0,0.6)), url("https://t4.ftcdn.net/jpg/01/06/32/13/240_F_106321309_U1xokp8gUIpTKdvRYvzhtmWl0pJnFQNZ.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.navbar{
  background-color: transparent;
}
.nav-link{
  font-size: 1.2rem;
  margin: 10px;
  position: relative;
  color: white;
  letter-spacing: 2px;
}
.nav-link:hover{
  color: rgb(194, 191, 191);
}
.nav-link::after{
  content: "";
  height: 1px;
  width: 0;
  background-color: white;
  position: absolute;
  left: 0;
  bottom:  -5px;
  transition: 0.5s;

}
.navbar-toggler{

  outline: none !important;
}
.navbar-toggler i{
  color: white;
  font-size: 2rem;
}
.nav-link:hover::after{
  color: white;
  width:100% ;
}

.header_text h1{
  font-size: 3.2rem;
  color: white;
  font-family: 'Lexend', sans-serif;
  letter-spacing: 1px;
  font-weight: 600;
  padding-top: 10px;
}
.header_text h1{
  color: white;
  margin-left: 20px;
  letter-spacing: 2px;
  font-size: 3.3rem;
  font-family: 'Lexend', sans-serif;

}
.header_text button {
  padding: 10px 20px;
  border: 1px solid white;
  background-color: transparent;
  margin: 10px;
  transition: 0.5s ease-in-out;
  border-radius: 3px;
  margin-top: 30px;
}
.header_text button a{
  text-decoration: none;
  font-size: 1.4rem;
  color: white;
  letter-spacing: 1px;
}
.header_text button:hover{
  background-color: rgb(15, 67, 150);
  border:  1px  solid rgb(15, 67, 150);
}

.insta{
  padding: 60px 20px;
}
.insta_card{
  width: 21rem;
  background-color: rgba(136, 93, 179, 0.432);
  overflow: hidden;
  height: 21rem;
  position: relative;
  margin-right: auto;
  margin-left: auto;
}
.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #34105084;
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: .5s ease;
}

.text {
  white-space: nowrap; 
  color: white;
  font-size: 20px;
  position: absolute;
  overflow: hidden;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}
.sub_card img{
  cursor: pointer;
}
.sub_card h5{
  font-family: 'Lexend', sans-serif;
  font-weight: 300;
  font-size: .9rem;
  margin-top: 10px;
  letter-spacing: 1px;
  cursor: pointer;

}
.sub_card i{
  color: rgb(242, 204, 13);
  cursor: pointer;
  
}
.icon_s{
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

}
.sub_card h6{
  font-family: 'Lexend', sans-serif;
  font-weight: 300;
  font-size: .8rem;
  cursor: pointer;

}
.insta_card:hover .overlay {
  height: 100%;
}
.insta a {
