/* =============================================================== Shop ==================================================================== */


.shop{
  height: 100%;
  height: 60vh;
  background-image:linear-gradient(rgba(0,0,0,0.9),rgba(0,0,0,0.6)), url("https://nudgem-prod.s3.amazonaws.com/uploads/collection_page_answers/2197/image/RKTbeans.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.shop_containt h4{
  font-family: 'Lexend', sans-serif;
  font-weight: 300;
  letter-spacing: 1px;
}
.shop_containt p {
  font-size: .6rem;
  letter-spacing: 1px;
  font-family: 'Josefin Sans', sans-serif !important; 
}
.shop_card{
  width: 20rem;
  height: 30rem;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  
}
.shop_card img{
  width: 100%;
  position: relative;
  height: 100%;
  object-fit: cover;
  transition: 0.9s ease-in-out;
}
.shop_btn{
  position: absolute;
  top: 45%;
  background-color: white;
  width: 90%;
  z-index: 99;
  margin-left: 15px;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-family: 'Lexend', sans-serif;
  border: none;
  outline: none !important;
  font-weight: 300;
}
.shop_btn:hover{
  background-color: rgb(105, 99, 99);
  color: white;
}
.shop_containt{
  padding: 60px  0px;
}
.shop_card img:hover{
  transform: scale(1.1);
}
.off{
  width: 90px;
  height: 90px;
  border-radius: 50%;
  background-color: black;
  border: 1px solid white;
  cursor: pointer;
  position: absolute;
  left: 50px;
