@media screen and (max-width:420px) {
  .navbar-brand img{
    height: 90px;
    width: 150px;
  }
  .card_loc{
     width: 20rem;
     /* height: 20rem; */
  }
  .wholes_text h1{
     font-size: 2.5rem;
  }
  .footer img{
   height: 120px;
   width: 170px;
  }
  .sub_text h1{
    /* font-size: 2.5rem; */
    display: none;
  }
  .sub_text p{
   /* display: none; */
   font-size: 1rem;
  }
  .shop_card{
    margin-top: 30px;
  }
  .shopby_btn{
    display: grid;
    grid-auto-rows: auto;
  }
  .off{
    display: none;
  }
  .about_text h1{
    font-size: 2.2rem;
  }
  .about_text button{
    padding: 5px 15px;
  }
  .about_text button a{
    font-size: 1rem;
  }
  .hero_card{
    width: 20rem;
    height: auto;
  }
  .card_loc{
